.aboutMe {
    grid-row: 1/2;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    column-gap: 200px;
    padding: 20px;
    background-color: rgba($color: #e6e6ff, $alpha: 1);
    border-bottom-left-radius: 30px;
    border-bottom-right-radius: 30px;
    z-index: 2;
    
    .identity {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    img {
        border-radius: 50%;
    }
}