.tech {
    grid-row: 2/3;
    display: flex;
    flex-direction: column;
    align-items: center;
    column-gap: 200px;
    background-color: white;
    width: 80%;
    height: fit-content;
    background-color: rgba($color: #e6e6ff, $alpha: 0.9);
    border-radius: 30px;

    .container {
        display: flex;
        flex-direction: row;
    }

    img {
        border-radius: 50%;
    }
}