.projects {
    grid-row: 3/4;
    height: fit-content;
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    background-color: rgba($color: #e6e6ff, $alpha: 0.8);
    border-radius: 30px;
    padding: 20px;


    h1 {
        align-self: center;
    }

    .project {
        display: flex;
        flex-direction: row;
        column-gap: 20px;
        padding: 50px;
        background-color: rgba($color: #e6e6ff, $alpha: 0.8);
        border: 3px solid rgba($color: #000066, $alpha: 0.8);
        border-radius: 30px;
        margin-bottom: 10px;


        .identification {
            display: flex;
            flex-direction: column;
            align-items: center;

            img {
                height: 400px;
                width: auto;
                border-radius: 20px;
            }
        }
        
        .description {
            display: flex;
            flex-direction: column;
            justify-content: center;
        }
    }

}