html,
body,
#root {
  width: 100vw;
  height: 100vh;
  color: #000033;
}

p, li {
  font-size: 20px;
}

.background {
  position: absolute;
  z-index: 0;
  height: 100%;
  width: auto;
}

#layout {
  display: grid;
  grid-template-rows: 1fr 1fr 4fr;
  width: 80%;
  z-index: 1;
  justify-items: center;
  align-items: center;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  overflow: hidden;
}